<template>
  <div>

    <page-loading v-if="loading"></page-loading>

    <div class="row" v-if="!loading && products.length > 0">
      <div :key="index" v-for="(product, index) in products" class="vx-col w-full sm:w-1/2 lg:w-1/4 mb-base">
        <vx-card>
          <img :src="product.image" alt="content-img" class="responsive rounded-lg">
          <div class="my-6">
            <h5 class="mb-2">{{product.title}}</h5>
            <p class="text-grey">{{product.description}}</p>
          </div>
          <vs-divider></vs-divider>
          <div class="flex justify-end flex-wrap">

            <span>
              <vs-button @click="goToProducts(product)" class="mt-4 mr-2" type="border" >View</vs-button>
            </span>
          </div>
        </vx-card>
      </div>
    </div>

  </div>

</template>

<script>

import axios from "../http/axios";
import mixins from "../mixins";

export default {
  mixins: [mixins],
  data () {
    return {
      loading: true,
      userPackages: [],
      products: []
    }
  },

  mounted() {
    this.getUserPackages();
  },

  methods: {

    getUserPackages(){
      this.loading = true;
      axios.get(`/api/store/user-packages/`,{
        headers:{
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200){
          this.userPackages = res.data;
          this.userPackages.forEach(userPackage => {
            this.products.push(...userPackage.package.products);
            this.products = this.products.filter((v,i,a)=>a.findIndex(v2=>(v2.id===v.id))===i); // remove duplicates
          })
        }else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'Something went wrong', 'danger');
        }
        this.loading = false;
      })
    },

    goToStore(){
      this.$router.push({name: "store-index"})
    },

    goToProducts(product){
      this.$router.push({name: product.url_name, params: {productId: product.id}})
    }
  }
}
</script>

